<template>
  <div class="layout">
    <Row>
      <Col>
        <NavBar :activeName="'experience'"></NavBar>
      </Col>
    </Row>
    <Row>
      <Col class="w1200">
        <Layout>
          <Row>
            <Breadcrumb :style="{margin: '16px 0'}">
              <BreadcrumbItem to="/"></BreadcrumbItem>
              <BreadcrumbItem></BreadcrumbItem>
            </Breadcrumb>
          </Row>
          <Row>
            <Content class="content">
              <Layout>
                <Content class="content-center">
                  <Timeline class="time-content-box">
                    <i-time-line-item
                      :color="(item.index % 2 === 0) ? '#FFAD2C' : '#50ABF8'"
                      v-for="item in data"
                      :key="item.index"
                    >
                      <p
                        class="time-time"
                        :class="{double: (item.index % 2 !== 0)}"
                      >{{item.timeShow}}</p>
                      <p class="time-content" :class="{double: (item.index % 2 !== 0)}">
                        <span class="time-title">{{item.title}}</span>
                        <span class="time-job">{{item.job}}</span>
                      </p>
                      <p class="time-content" :class="{double: (item.index % 2 !== 0)}">
                        <i>{{item.content}}</i>
                      </p>
                      <p
                        class="time-content"
                        :class="{double: (item.index % 2 !== 0)}"
                        v-for="p in item.describe"
                        :key="p.index"
                      >{{p}}</p>
                    </i-time-line-item>
                  </Timeline>
                </Content>
              </Layout>
            </Content>
          </Row>
        </Layout>
      </Col>
    </Row>
    <Row>
      <Col>
        <Weibo></Weibo>
      </Col>
    </Row>
    <Row>
      <Col>
        <Footer></Footer>
      </Col>
    </Row>
  </div>
</template>

<script>
import moment from "moment";
import _ from "lodash";
import NavBar from "../components/NavBar.vue"; // 
import Footer from "../components/Footer.vue"; // 
import Weibo from "../components/Weibocom.vue"; // 
export default {
  name: "Experience",
  components: {
    NavBar, // 
    Footer, // 
    Weibo // 
  },
  data() {
    return {
      msg: "Experience",
      data: [
        {
          startTime: "2019-8-26 00:00:00",
          endTime: "",
          title: "",
          content: "",
          job: "",
          describe: [
            "1ReactReduxHTMLCSS",
            "2",
            "3jsui",
            "4html/css",
            "5bug"
          ]
        },
        {
          startTime: "2018-7-14 00:00:00",
          endTime: "2019-8-16 00:00:00",
          title: "",
          content: "",
          job: "",
          describe: [
            "1",
            "2demo",
            "3H5Vue.js",
            "4UI",
            "5web",
            "6",
            "7bug",
            "8"
          ]
        },
        {
          startTime: "2017-4-17 00:00:00",
          endTime: "2018-7-14 00:00:00",
          title: "",
          content: "",
          job: "",
          describe: [
            "1",
            "2UIWeb",
            "3H5AngularJSVue.js",
            "4UI",
            "5web",
            "6,",
            "7",
            "8bug,",
            "9",
            "10"
          ]
        },
        {
          startTime: "2016-8-1 00:00:00",
          endTime: "2017-4-17 00:00:00",
          title: "",
          content: "",
          job: "",
          describe: [
            "1UIWeb",
            "2H5JSjQPC",
            "3UI",
            "4",
            "5bug,"
          ]
        }
      ]
    };
  },
  methods: {
    /**
     * 
     */
    getList() {
      var self = this;
      self.$loadingBar.finish();
      console.log("json");
      self.initList();
    },
    /**
     * 
     */
    initList() {
      var self = this;
      self.data = _.orderBy(self.data, ["startTime"], ["desc"]);
      _.each(self.data, function(v, k) {
        v.timeShow =
          moment(v.startTime).format("YYYY.MM") +
          " - " +
          (moment(v.endTime).format("YYYY.MM") !== "Invalid date"
            ? moment(v.endTime).format("YYYY.MM")
            : "");
        v.index = k;
      });
    },
    init() {
      var self = this;
      self.getList();
    }
  },
  created() {
    var self = this;
    self.init();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.layout {
  min-width: 800px;
  position: relative;
  border-radius: 4px;
  overflow: hidden;
}
.w1200 {
  margin: 0 auto 50px;
  width: 1200px;
}
.content {
  background: #fff;
}
.content-center {
  padding: 24px;
  background: #fff;
  .time-content-box {
    margin-top: 30px;
    margin-left: 50%;
    .time-time {
      position: relative;
      top: -4px;
      color: #ffffff;
      text-align: center;
      width: 140px;
      height: 25px;
      line-height: 25px;
      font-size: 14px;
      font-weight: bold;
      background: #ffad2c;
      border-radius: 25px;
    }
    .time-time.double {
      position: relative;
      left: -175px;
      background: #50abf8;
    }
    .time-content {
      padding-left: 5px;
      padding-top: 5px;
      .time-title {
        font-size: 18px;
      }
      .time-job {
        font-size: 14px;
        color: #888;
      }
      .time-job:before {
        display: inline-block;
        vertical-align: middle;
        margin: 0 5px;
        width: 40px;
        height: 1px;
        background: #888;
        content: "";
      }
    }
    .time-content.double {
      position: relative;
      left: -590px;
      text-align: right;
    }
  }
}
</style>



