<template>
  <div class="layout">
    <Row>
      <Col>
        <NavBar :activeName="'evaluation'"></NavBar>
      </Col>
    </Row>
    <Row>
      <Col class="w1200">
        <Layout>
          <Row>
            <Breadcrumb :style="{margin: '16px 0'}">
              <BreadcrumbItem to="/"></BreadcrumbItem>
              <BreadcrumbItem></BreadcrumbItem>
            </Breadcrumb>
          </Row>
          <Row>
            <Content class="content">
              <Layout>
                <Content class="content-center">
                  <Row>
                    <Col class="content-text">{{data.passage1.content}}</Col>
                    <Col class="content-text">{{data.passage2.content}}</Col>
                    <Col class="content-text">{{data.passage3.content}}</Col>
                    <Col class="content-text">{{data.passage4.content}}{{data.passage5.content}}</Col>
                    <Col class="content-text">{{data.passage6.content}}</Col>
                    <Col class="content-text">{{data.passage7.content}}{{data.passage8.content}}</Col>
                    <Col class="content-text">{{data.passage9.content}}</Col>
                  </Row>
                </Content>
              </Layout>
            </Content>
          </Row>
        </Layout>
      </Col>
    </Row>
    <Row>
      <Col>
        <Weibo></Weibo>
      </Col>
    </Row>
    <Row>
      <Col>
        <Footer></Footer>
      </Col>
    </Row>
  </div>
</template>

<script>
import NavBar from "../components/NavBar.vue"; // 
import Footer from "../components/Footer.vue"; // 
import Weibo from "../components/Weibocom.vue"; // 
export default {
  name: "Evaluation",
  components: {
    NavBar, // 
    Footer, // 
    Weibo // 
  },
  data() {
    return {
      data: {
        passage1: {
          msg: "",
          content: ""
        },
        passage2: {
          msg:
            "",
          content: ""
        },
        passage3: {
          msg:
            "",
          content: ""
        },
        passage4: {
          msg: "",
          content: ""
        },
        passage5: {
          msg:
            "Gitbook",
          content: ""
        },
        passage6: {
          msg:
            "npmshell",
          content: ""
        },
        passage7: {
          msg:
            "",
          content: ""
        },
        passage8: {
          msg:
            "",
          content: ""
        },
        passage9: {
          msg:
            "",
          content: ""
        }
      }
    };
  },
  methods: {
    /**
     * 
     * @param type 
     * @param millis 
     */
    writing(type, millis) {
      var self = this;
      var index = 0;
      var data = self.data["passage" + type];
      var ID = setInterval(function() {
        data.content += data.msg[index];
        index++;
        if (index === data.msg.length) {
          clearInterval(ID);
          if (type < Object.keys(self.data).length) {
            setTimeout(function() {
              self.writing(type + 1, millis);
            }, millis);
          }
        }
      }, 70);
    }
  },
  created() {
    var self = this;
    self.$loadingBar.finish();
    self.writing(1, 500);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.layout {
  min-width: 800px;
  position: relative;
  border-radius: 4px;
  overflow: hidden;
}
.w1200 {
  margin: 0 auto 50px;
  width: 1200px;
}
.content {
  min-height: 600px;
  background: #fff;
}
.content-center {
  padding: 24px;
  background: #fff;
  .content-text {
    font-size: 18px;
    line-height: 3em;
    text-indent: 2em;
  }
}
</style>



